import { Vector4 } from "../../../../maths/Vector4";
import { Viewport } from "../../../../maths/Viewport";
import { SetViewportCMD } from "../../../DriverDesign/3DRenderPass/IRender3DCMD";
import { RenderCMDType } from "../../../DriverDesign/RenderDevice/IRenderCMD";
export class WebGPUSetViewportCMD extends SetViewportCMD {
    get viewport() {
        return this._viewport;
    }
    set viewport(value) {
        this._viewport = value;
    }
    get scissor() {
        return this._scissor;
    }
    set scissor(value) {
        this._scissor = value;
    }
    constructor() {
        super();
        this.type = RenderCMDType.ChangeViewPort;
        this.scissor = new Vector4();
        this.viewport = new Viewport();
    }
    apply(context) {
        context.setViewPort(this.viewport);
        context.setScissor(this.scissor);
    }
}
